            <v-btn color="white" @click="returnState(0)" height="52" width="120" class="rounded-0 mx-2">بازگشت</v-btn>
          </v-col>

          <v-col v-if="state===2" cols="12" md="12"
                 class="d-flex justify-center flex-column align-center rounded py-8 px-10">
            <v-img width="78" height="78" src="../assets/image/user.svg"></v-img>
            <p style="font-size: 18px" class="pt-9 pb-10 text-center">مشخصات خود را وارد کنید</p>
