</template>

<script>
import OtherPageSlide from "@/components/Misc/OtherPageSlide";
import {isChars} from "@/components/Misc/isChars";
import axios from "axios";

export default {
  name: "PricingApp",
  mixins: [isChars],
  components: {OtherPageSlide},
  created() {
    this.fetchPricingTypes();
  },
  data() {
    return {
      state: 0,
      pricingTypes: [],
      type: this.$route.params.id,
      full_name: null,
      phone_number: null,
      total_infrastructure: null,
      facades_num: 0,
      yard_and_roof_garden_meterage: null,
      render_num: null,
      interior_design_area: null,
      interior_render_num: null,
      project_duration: null,
      need_to_design_plan: false,
      need_to_interior_design: false,
      brigade_plans_num: 0,
      units_brigade: [],
      ex3: {label: 'روز', val: 30, color: 'primary'},
      final_result : 0,
    }
  },
  watch: {
    state () {
      window.scrollTo({
        top: document.querySelector('#topPrice').offsetTop - 130,
        behavior: 'smooth'
      })
    },
    '$route'() {
      if(!this.$route.params.id) this.state = 0;
      this.full_name = null;
      this.phone_number = null;
      this.total_infrastructure = null;
      this.facades_num = 0;
      this.yard_and_roof_garden_meterage = null;
      this.render_num = null;
      this.interior_design_area = null;
      this.interior_render_num = null;
      this.project_duration = null;
      this.need_to_design_plan = false;
      this.need_to_interior_design = false;
      this.brigade_plans_num = 0;
      this.units_brigade = [];
      this.ex3 = {label: 'روز', val: 30, color: 'primary'};
    },
    brigade_plans_num(val) {
      this.units_brigade = new Array(val);
    },
    need_to_interior_design(val) {
      if (!val) {
        this.interior_design_area = null;
        this.interior_render_num = null;
      }
    }
  },
  methods: {
    sendPricing() {
      axios.post(this.$store.state.host + '/pricing/api/v1/create-pricing', {
        type: this.$route.params.id,
        full_name: this.full_name,
        phone_number: this.phone_number,
        total_infrastructure: this.total_infrastructure,
        brigade_plans_num: this.brigade_plans_num,
        units_brigade: this.units_brigade,
        facades_num: this.facades_num,
        yard_and_roof_garden_meterage: this.yard_and_roof_garden_meterage,
        render_num: this.render_num,
        interior_design_area: this.interior_design_area,
        interior_render_num: this.interior_render_num,
        project_duration: this.ex3.val,
        need_to_design_plan: this.need_to_design_plan,
        need_to_interior_design: this.need_to_interior_design
      }).then(response => {
        this.final_result = response.data.data.final_result;
        this.state = 3;
        this.notify('محاسبه قیمت ثبت شد .', 'primary')
      }).catch(error => {
        this.errorHandle(error.response)
      })
    },
    changeState(state, id) {
      this.state = state;
      if (id) {
        this.$router.push({name: 'Pricing', params: {id}}).catch(() => {
        })
      }
    },
    returnState(state) {
      if (this.state === 1) {
        this.$router.push({name: 'Pricing'}).catch(() => {
        })
      }
      this.state = state;
    },
    fetchPricingTypes() {
      this.loading = true;
      axios.get(this.$store.state.host + '/pricing/api/v1/pricing-types').then(response => {
        this.pricingTypes = response.data.data;
        this.loading = false;
      }).catch(error => {
        this.errorHandle(error.response)
      })
    }
  }
}
</script>

